<template>
  <div class="pager-container" v-if="total > 0">
    <!-- 总页数小于0的情况下不显示分页 -->
    <a @click="hanldClick(1)" :class="{ disabled: current === 1 }">首页</a>
    <!-- 首页被点击时把页面1传进去    disabled这个class有没有取决于当前页面是不是等于1，有这个属性代表是不能被点击的 -->
    <a @click="hanldClick(current - 1)" :class="{ disabled: current === 1 }"
      >上一页</a
    >
    <!-- 当上一页被点击时，把当前页数-1的值传过去，  disabled这个class有没有取决于当前页面是不是等于1 有这个属性代表是不能被点击的 -->
    <a
      @click="hanldClick(item)"
      v-for="(item, i) in newArray"
      :key="i"
      :class="{ active: current === item }"
      >{{ item }}</a
    >
    <!-- 这是中间的页面，当其中一个没点击时，就把被点击的页面传过去
         并且循环newArray(这个数组是通过计算属性算出来的，请看computed)生成a标签，
     -->
    <a
      @click="hanldClick(current + 1)"
      :class="{ disabled: current === pageNumber }"
      >下一页</a
    >
    <!-- 下一页被点击时传递当前页+1 -->
    <a
      @click="hanldClick(pageNumber)"
      :class="{ disabled: current === pageNumber }"
      >尾页</a
    >
    <!-- 尾页被点击时，传递总页数pageNumber(通过计算属性算出来的) -->
  </div>
</template>

<script>
export default {
  props: {
    //分页组件需要父组件传递四个属性，如下
    current: {
      //当前页码
      type: Number,
      default: 1,
    },
    total: {
      //总数量
      type: Number,
    },
    limit: {
      //页容量
      type: Number,
      default: 10,
    },
    visibleNumber: {
      //可见页码数
      type: Number,
      default: 10,
    },
  },
  computed: {
    pageNumber() {
      //根据当前总页数和页容量算出一共有多少页
      return Math.ceil(this.total / this.limit);
    },
    visibleMin() {
      //计算出最小页码，最小页面的情况是，每页显示10条数据，当前页是8时，他就是4，
      //也就是说，最小页面 == 当前页 - 可见页码 /2
      let min = this.current - Math.ceil(this.visibleNumber / 2);
      if (min < 1) {
        min = 1;
        //最小页码不能比1小，最小为1
      }
      return min;
    },
    visibleMax() {
      //最大页面数 == 最小页面数 + 可见页码数 -1
      let max = this.visibleMin + this.visibleNumber - 1;
      if (max > this.pageNumber) {
        max = this.pageNumber;
        //最大页面不能比页面数大，最大为页面数
      }
      return max;
    },
    newArray() {
      let number = [];
      for (let i = this.visibleMin; i <= this.visibleMax; i++) {
        //根据最小页码和最大页码生成一个页码数组
        number.push(i);
      }
      return number;
    },
  },
  methods: {
    hanldClick(newPager) {
      if (newPager < 1) {
        //点击过后传过来的新页码比1小，那就 = 1；
        newPager = 1;
      }

      if (newPager > this.pageNumber) {
        //如果传递过来的新页码大于页码数，那就 = 页码数
        newPager = this.pageNumber;
      }

      if (newPager === this.current) {
        //如果传递过来的新页码和当前页码一样，不做任何处理
        return;
      }
      this.$emit("pagerChange", newPager);
    },
  },
};
</script>

<style scoped lang="less">
@import "~@/styles/var.less";
.pager-container {
  display: flex;
  justify-content: center;
  padding: 20px;
  a {
    font-weight: bold;
    cursor: pointer;
    color: @primary;
    text-decoration: none;
    margin: 0 10px;
    &.disabled {
      cursor: not-allowed;
      color: @dark;
    }
    &.active {
      color: rgb(255, 94, 0);
    }
  }
}
</style>
