<template>
  <div
    class="blog-creagoryid-container"
    ref="container"
    v-if="!isLoading"
    v-loading="isLoading"
  >
    <h2>文章列表</h2>
    <RightList :list="list" @select="hanldeSelect" />
  </div>
</template>

<script>
import fetchData from "@/mixins/fetchData.js";
import { getBlogTypes } from "@/api/blog.js";
import RightList from "./rightList.vue";
export default {
  mixins: [fetchData([])],
  components: {
    RightList,
  },
  methods: {
    async fetchData() {
      const resp = await getBlogTypes();
      return resp.data;
    },
    hanldeSelect(item) {
      const query = {
        page: 1,
        limit: this.limit,
      };
      if (item.id === -1) {
        this.$router.push({
          name: "Blog",
          query,
        });
      } else {
        this.$router.push({
          name: "CreagoryBlog",
          query,
          params: {
            creagoryid: item.id,
          },
        });
      }
    },
  },
  computed: {
    creagoryid() {
      return +this.$route.params.creagoryid || -1;
    },
    list() {
      const totalArticleCount = this.data.reduce(
        (a, b) => a + b.articleCount,
        0
      );
      const result = [
        { name: "全部", id: -1, articleCount: totalArticleCount },
        ...this.data,
      ];
      return result.map((it) => ({
        ...it,
        isSelect: it.id === this.creagoryid,
        aside: `${it.articleCount}篇`,
      }));
    },
    limit() {
      return +this.$route.query.limit || 10;
    },
  },
};
</script>

<style scoped lang="less">
.blog-creagoryid-container {
  position: relative;
  overflow: hidden;
  width: 160px;
  height: 100%;
  padding: 20px;
  h2 {
    margin: 0;
  }
}
</style>
