<template>
  <div class="blog-list-container" ref="container" v-loading="isLoading">
    <ul>
      <li v-for="item in data.rows" :key="item.id">
        <div class="thumb">
          <RouterLink
            :to="{
              name: 'BlogDetail',
              params: {
                id: item.id,
              },
            }"
          >
            <img v-lazy="item.thumb" :alt="item.title" :title="item.title" />
          </RouterLink>
        </div>
        <div class="main">
          <RouterLink
            :to="{
              name: 'BlogDetail',
              params: {
                id: item.id,
              },
            }"
          >
            <h2>{{ item.title }}</h2>
          </RouterLink>
          <div class="aside">
            <span>日期：{{ formatData(item.createDate) }}</span>
            <span>浏览：{{ item.scanNumber }}</span>
            <span>评论：{{ item.commentNumber }}</span>
            <RouterLink
              :to="{
                name: 'CreagoryBlog',
                params: {
                  creagoryid: item.category.id,
                },
              }"
              class=""
              >分类:{{ item.category.id }}</RouterLink
            >
          </div>
          <div class="desc">
            {{ item.description }}
          </div>
        </div>
      </li>
    </ul>
    <!-- 显示分页组件 -->
    <Pager
      v-if="data"
      :current="routeInfo.page"
      :limit="routeInfo.limit"
      :total="data.total"
      @pagerChange="hanldePageChange"
    />
    <ToTop />
  </div>
</template>

<script>
import fetchData from "@/mixins/fetchData.js";
import { getBlogs } from "@/api/blog.js";
import { formatData } from "@/utils/";
import Pager from "@/components/Pager";
import ToTop from "@/components/ToTop";
import setMainScroll from "@/mixins/setMainScroll";
export default {
  mixins: [fetchData({}), setMainScroll("container")],
  components: {
    Pager,
    ToTop,
  },
  methods: {
    formatData,
    async fetchData() {
      const resp = await getBlogs(
        this.routeInfo.page,
        this.routeInfo.limit,
        this.routeInfo.creagoryid
      );
      return resp.data;
    },
    hanldePageChange(newPage) {
      const query = {
        page: newPage,
        limit: this.routeInfo.limit,
      };
      if (this.routeInfo.creagoryid === -1) {
        // 当前是全部文章
        // /blog?page${newPage}&limit=10
        this.$router.push({
          name: "Blog",
          query,
        });
      } else {
        this.$router.push({
          name: "CreagoryBlog",
          query,
          params: {
            creagoryid: this.routeInfo.creagoryid,
          },
        });
      }
    },
  },
  computed: {
    routeInfo() {
      const page = +this.$route.query.page || 1;
      const limit = +this.$route.query.limit || 10;
      const creagoryid = +this.$route.params.creagoryid || -1;
      return {
        page,
        limit,
        creagoryid,
      };
    },
  },
  watch: {
    async $route() {
      this.$refs.container.scrollTop = 0;
      this.isLoading = true;
      this.data = await this.fetchData();
      this.isLoading = false;
    },
  },
};
</script>

